@use "../src/helpers/sass/variables" as *;
@use "../src/helpers/sass/mixin" as *;

// basic styles
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Tajawal", sans-serif;
  font-size: 18px;
  padding: 0;
  margin: 0;
}

h2 {
  padding: 0 0 20px;
  margin: 0;
}

@include headerContainerPadding(0 100px);

@include breakPoints(lg) {
  @include headerContainerPadding(37.5px 75px);
}

// header
header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 11;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .logo {
    a img {
      width: 80px;
      height: 70px;
    }
  }

  .dropmenu {
    display: none;
    cursor: pointer;
    border: 2px solid #000;
    font-size: 25px;
    padding: 3px 6px;
    border-radius: 3px;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $gray-color;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;

    &.active {
      animation: fadeIn 0.3s ease forwards;
      visibility: visible;
    }

    .close {
      background-color: $red-color;
      color: #fff;
      cursor: pointer;
      outline: none;
      border: none;
      font-weight: bold;
      width: 30px;
      height: 30px;
      margin: 30px;
      border-radius: 50%;
      position: relative;
      div {
        @include center("both");
      }
    }

    .menu {
      @include center("both");
      text-align: center;
      div {
        padding: 3px;
        a {
          color: $green-color;
          text-decoration: none;
          font-weight: bold;
        }
      }
    }
  }

  nav {
    display: flex;
    gap: 25px;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
    div a {
      color: $green-color;
      text-decoration: none;
      @include time;
      &:hover {
        color: $red-color;
      }
    }
  }
}

@include breakPoints(md) {
  @include headerContainerPadding(25px 50px);
  header {
    .dropmenu {
      display: block;
    }
    nav {
      display: none;
    }
  }
}

@include breakPoints(lg) {
  @include headerContainerPadding(10px 25px, 16px);
}
